import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { closeAppAction } from 'store/actions/app';
import { ButtonViews } from 'components/Button/Button';
import { DialogPage, DialogButton, DialogTitle } from 'components/DialogPage/DialogPage';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { layout } from 'style/const/layout';

import backgroundImage from './Success.assets/bg.png';

const StyledBackground = styled.div`
    position: absolute;
    top: ${-1 * layout.headerHeightPx}px;
    left: 0;

    width: ${layout.widthPx}px;
    height: ${layout.heightPx}px;

    background-image: url('${backgroundImage}');
    background-size: contain;
    background-repeat: no-repeat;
`;

const decorations = <StyledBackground />;

export const Success: React.FC = () => {
    const dispatch = useDispatch();
    const { deliveryTime } = useSelector(({ delivery }) => delivery);

    const buttons = React.useMemo(
        () => (
            <DialogButton
                shouldFocusOnMount
                view={ButtonViews.primary}
                onClick={() => {
                    dispatch(closeAppAction());
                }}
            >
                Очень жду
            </DialogButton>
        ),
        [dispatch],
    );

    return (
        <DialogPage buttons={buttons} decorations={decorations}>
            <DialogTitle>
                <MultilineText text={`Заказ принят. \nБудем у вас через \n${deliveryTime} минут.`} />
            </DialogTitle>
        </DialogPage>
    );
};
